<template>
  <div class="main">
    <van-nav-bar title="服务中心" left-text="" left-arrow @click-left="$toPage(-1)" >
      <van-icon name="arrow-left" size="24px" color="#666666" slot="left" />
    </van-nav-bar>
    <div class="head">
      <div class="head-first" @click="callPhone">
        <div class="img">
          <img src="../../assets/phone.png" alt="">
        </div>
        <p class="p-head">客服热线</p>
      </div>
      <div class="head-second" @click="$openTel(serviceQQ)">
        <div class="img">
          <img src="../../assets/vx.png" alt="">
        </div>
        <p class="p-head">在线客服</p>
      </div>
    </div>
    <p class="nbsp">问题分类</p>
    <van-cell v-for="(item,index) in list" :key="index" :title="item.title" @click="click(item.router)" is-link />
  </div>
</template>
<script>
  export default {
    data() {
      return {
        tel: null,
        serviceQQ: null,
        list: [{
            title: '常见问题',
            router: 'question'

          },
          {
            title: '订单问题',
            router: 'send'
          },
          {
            title: '售后问题',
            router: 'after'
          },
          // {
          //   title: '优惠问题',
          //   router: 'less'
          // },
          // {
          //   title: '秒杀问题',
          //   router: 'seckill'
          // },
          // {
          //   title: '支付问题',
          //   router: 'pay'
          // },
          // {
          //   title: '积分问题',
          //   router: 'gender'
          // },
          // {
          //   title: '会员问题',
          //   router: 'vip'
          // },
          {
            title: '时效问题',
            router: 'timer'
          },
          {
            title: '其他问题',
            router: 'other'
          },
          {
            title: '隐私协议',
            router: 'yinsi'
          },
          {
            title: '用户服务协议',
            router: 'fuwu'
          },
          {
            title: '法律声明',
            router: 'shengming'
          },
        ],
        loading: false,
        finished: false
      };
    },
    mounted() {
      this.getServiceInfo()
    },
    methods: {
      callPhone() {
        JNB._requestNative("callPhone", this.tel);
      },

      click(router) {
        this.$toPage(router)
      },
      //获取客服信息
      getServiceInfo() {
        JNB.query({
          serverName: "globalServer",
          cls: "WebAppModule",
          method: "customerInfo",
          callback: (data) => {
            data = JSON.parse(data);
            if (data.code == 200) {
              data.data = JSON.parse(data.data);
              this.tel = data.data.compTel;
              this.serviceQQ = data.data.customList[Math.floor(Math.random())*data.data.customList.length].qq;
            }
          },
        })
      }

    }
  }
</script>

<style scoped>
  .p-head {
    font-size: 28px;
    color: #333333;
    font-weight: bold;
    height: 27px;
  }

  .nbsp {
    text-indent: 32px;
    color: #666666;
  }

  .img {
    background-size: cover;
    height: 62px;
    width: 64px;
    margin: 0 auto;
    margin-top: 20px;
  }

  img {
    height: 62px;
    width: 64px;
  }

  .head {
    margin: 0 auto;
    margin-top: 30px;
    position: relative;
    height: 172px;
    background-color: white;
    width: 100%;
  }

  .head-first {
    background-size: cover;
    position: absolute;
    top: 0px;
    left: 0px;
    width: 50%;
    height: 100%;
    text-align: center;
    border-right: 1px solid #E6E6E6;
  }

  .head-second {
    position: absolute;
    top: 0px;
    right: 0px;
    width: 50%;
    height: 100%;
    text-align: center
  }

  .main {
    position: relative;
  }

  div {
    font-size: 30px
  }

  .van-cell:not(:last-child):after {
    left: 0px;
    border-bottom: 1px solid slategrey
  }

  .van-cell {
    color: #666666;
    font-size: 28px;
  }

  .van-cell[data-v-6eab2c92]:not(:last-child):after {
    border-bottom: 1px solid #E6E6E6;
  }

  .van-nav-bar {
    background-color: #ffffff;
  }


  .van-nav-bar__title {
    font-family: PingFang-SC-Regular;
    color: #333333;
    font-size: 36px;
  }
</style>
